import { LitElement, css, html, unsafeCSS } from "lit";
import { customElement } from "lit/decorators.js";
import cssStylesheet from "./navigation-sticky.css?inline";

/**
 * An example element.
 *
 * @slot - This element has a slot
 * @csspart button - The button
 */
@customElement("fabric-ui-navigation-sticky")
export class NavigationSticky extends LitElement {
  /**
   * Style sheet used by this component.
   */
  static styles = css`
    ${unsafeCSS(cssStylesheet)}
  `;

  observer?: IntersectionObserver;
  content?: HTMLDivElement | null;
  lastScrollTop = 0;
  lastDirection?: "UP" | "DOWN";

  private handlePageScroll = () => {
    const currentScrollTop = window.scrollY; // or window.pageYOffset

    if (currentScrollTop > this.lastScrollTop) {
      if (this.lastDirection !== "DOWN") {
        this.lastDirection = "DOWN";
        this.content?.classList.remove(
          "fbvc-navigation-sticky__content--stuck-visible"
        );
      }
    } else if (this.lastDirection !== "UP") {
      this.lastDirection = "UP";
      this.content?.classList.add(
        "fbvc-navigation-sticky__content--stuck-visible"
      );
    } else if (
      currentScrollTop < 200 &&
      this.content?.classList.contains("fbvc-navigation-sticky__content--stuck-visible")
    ) {
      // Remove class if we are near the top
      this.content?.classList.remove("fbvc-navigation-sticky__content--stuck-visible");
    }
    this.lastScrollTop = currentScrollTop; // Update the last scroll position
  };

  connectedCallback() {
    super.connectedCallback();
    this.observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (!this.content) {
            return;
          }
          if (entry.isIntersecting) {
            console.log("Element is in view!");
            this.content.classList.remove(
              "fbvc-navigation-sticky__content--stuck"
            );
            // Element is visible
          } else {
            console.log("Element scrolled out of view.", this.content);
            this.content.classList.add(
              "fbvc-navigation-sticky__content--stuck"
            );
            // Element is no longer visible
          }
        });
      },
      {
        threshold: 0.5,
      }
    );

    window.addEventListener("scroll", this.handlePageScroll);
  }

  firstUpdated() {
    const element: HTMLDivElement | null | undefined =
      this.shadowRoot?.querySelector(".fbvc-navigation-sticky");
    this.content = this.shadowRoot?.querySelector(
      ".fbvc-navigation-sticky__content"
    );

    if (element && this.observer) {
      element.style.minHeight = `${
        this.content?.getBoundingClientRect().height
      }px`;
      this.observer.observe(element);
    }
  }

  disconnectedCallback() {
    if (this.observer) {
      this.observer.disconnect();
    }
    window.removeEventListener("scroll", this.handlePageScroll);
    super.disconnectedCallback();
  }

  protected render() {
    return html`
      <div class="fbvc-navigation-sticky">
        <div class="fbvc-navigation-sticky__content">
          <slot></slot>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    "fabric-ui-navigation-sticky": NavigationSticky;
  }
}
