import { LitElement, css, html, unsafeCSS } from "lit";
import { customElement, property } from "lit/decorators.js";
import cssStylesheet from "./parametric-lines.css?inline";
// import glsl from "glslify";
import * as THREE from "three";
import type { ParametricLinesProps } from "./parametric-lines.type";
import fragmentShaderRaw from "./shaders/main.frag?raw";
import vertexShaderRaw from "./shaders/main.vert?raw";

const vertexShader = vertexShaderRaw.replaceAll(
  /#(version|define).*?(\n|$)/g,
  ""
);
const fragmentShader = fragmentShaderRaw.replaceAll(
  /#(version|define).*?(\n|$)/g,
  ""
);

export default function createLineGeometry(
  numSides = 1,
  subdivisions = 50,
  length = 1,
  radius = 0.05 // Increase this value for thicker lines
) {
  const baseGeometry = new THREE.CylinderGeometry(
    radius, // top radius
    radius, // bottom radius
    length, // cylinder height
    numSides, // number of radial segments
    subdivisions, // height segments
    true // openEnded
  );

  baseGeometry.rotateZ(Math.PI / 2); // Aligning the cylinder along the X axis

  const positions = baseGeometry.getAttribute("position").array;
  const vertexCount = baseGeometry.getAttribute("position").count;

  const posArray = new Float32Array(vertexCount * 3);
  for (let i = 0; i < positions.length; i++) {
    posArray[i] = positions[i];
  }

  baseGeometry.dispose(); // Clean up memory

  return { posArray };
}

function curvedLine(
  material: THREE.RawShaderMaterial,
  vertices: Float32Array<ArrayBuffer>
) {
  const geometry = new THREE.BufferGeometry();

  // itemSize = 3 because there are 3 values (components) per vertex
  geometry.setAttribute("position", new THREE.BufferAttribute(vertices, 3));

  const mesh = new THREE.Line(geometry, material);

  return mesh;
}

/**
 * An example element.
 *
 * @slot - This element has a slot
 * @csspart button - The button
 */
@customElement("fabric-ui-parametric-lines")
export class ParametricLines extends LitElement {
  /**
   * Style sheet used by this component.
   */
  static styles = css`
    ${unsafeCSS(cssStylesheet)}
  `;

  observer?: IntersectionObserver;
  pauseAnimation = false;
  lastTime = Date.now();
  expectedDiff = 50;
  numSides = 1;
  lines: ReturnType<typeof curvedLine>[] = [];
  group = new THREE.Group();
  width = window.innerWidth * window.devicePixelRatio;
  height = window.innerHeight * window.devicePixelRatio;
  renderer = new THREE.WebGLRenderer({ antialias: true });
  camera = new THREE.PerspectiveCamera(70, this.width / this.height, 0.01, 10);
  scene = new THREE.Scene();

  @property({ type: Number })
  accessor subdivisions: ParametricLinesProps["subdivisions"] = 800;
  @property({ type: Number })
  accessor totalMeshes: ParametricLinesProps["totalMeshes"] = 24;

  @property({ type: Number }) accessor rSample2Blend: number = 0.9;
  @property({ type: Number }) accessor rSample3Blend: number = 1;
  @property({ type: Number }) accessor rSample4Blend: number = 0;
  @property({ type: Number }) accessor rSample5Blend: number = 0.2;
  @property({ type: Number }) accessor thetaMultiplier: number = 4.0;
  @property({ type: Number }) accessor phiPIMult: number = 0.05;
  @property({ type: String }) accessor color: string = "#FFA500";
  @property({ type: Number }) accessor opacity: number = 1.0;
  @property({ type: Number }) accessor groupX: number = 1;
  @property({ type: Number }) accessor groupY: number = 0.7;
  @property({ type: Number }) accessor groupZ: number = -2;
  @property({ type: Number }) accessor scale: number = 2;
  @property({ type: Number }) accessor fps: number = 60;

  connectedCallback() {
    super.connectedCallback();
    this.observer = new IntersectionObserver(
      (args) => {
        const [wrapperObservationEntry] = args;
        this.pauseAnimation = !wrapperObservationEntry.isIntersecting;
      },
      {
        threshold: 0.5,
      }
    );
    window.addEventListener("resize", this.handleResize);
  }

  disconnectedCallback() {
    this.observer?.disconnect();
    window.removeEventListener("scroll", this.handleResize);
    super.disconnectedCallback();
  }

  protected firstUpdated(): void {
    // If element did not render properly return early
    if (this.renderRoot instanceof ShadowRoot === false) {
      return;
    }

    // Get canvas element wrapper
    const wrapper = this.renderRoot.getElementById("canvas-wrapper");

    // If wrapper did not initialize properly return early
    if (!wrapper) {
      return;
    }

    this.expectedDiff = 1000 / this.fps;

    this.observer?.observe(wrapper);

    // Configure canvas element to make sure is ready!
    this.scene.background = new THREE.Color(0xffffff);
    this.renderer.setSize(this.width, this.height);
    this.renderer.setAnimationLoop(this.handleAnimate);
    wrapper.appendChild(this.renderer.domElement);
    const lineData = createLineGeometry(this.numSides, this.subdivisions);

    const sharedUniforms = {
      time: { value: 0 },
      rSample2Blend: { value: this.rSample2Blend },
      rSample3Blend: { value: this.rSample3Blend },
      rSample4Blend: { value: this.rSample4Blend },
      rSample5Blend: { value: this.rSample5Blend },
      thetaMultiplier: { value: this.thetaMultiplier },
      phiPIMult: { value: this.phiPIMult },
      color: { value: new THREE.Color(this.color) },
      opacity: { value: this.opacity },
    };

    //create a group and add the two cubes
    //These cubes can now be rotated / scaled etc as a group
    this.group.scale.set(this.scale, this.scale, this.scale);
    this.group.rotation.set(Math.PI / 4, 0, 0);
    this.group.position.set(this.groupX, this.groupY, this.groupZ);

    // Draw the lines!
    for (let i = 0; i < this.totalMeshes; i++) {
      const t = this.totalMeshes <= 1 ? 0 : i / (this.totalMeshes - 1);
      const material = new THREE.RawShaderMaterial({
        side: THREE.DoubleSide,
        defines: {
          lengthSegments: parseFloat(this.subdivisions.toFixed(1)),
        },
        glslVersion: THREE.GLSL3,
        fragmentShader,
        vertexShader,
        uniforms: {
          ...sharedUniforms,
          index: { value: t },
        },
        transparent: true,
      });

      const mesh = curvedLine(material, lineData.posArray);
      this.group.add(mesh);
      this.lines.push(mesh);
    }

    this.scene.add(this.group);

    // For now just make sure camera is at 0, movement happens on the group
    this.camera.position.z = 0;
  }

  private handleResize = () => {
    this.width = window.innerWidth * window.devicePixelRatio;
    this.height = window.innerHeight * window.devicePixelRatio;
    this.camera.aspect = this.width / this.height;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(this.width, this.height);
  };

  /**
   *
   * @param time milliseconds since animation started
   */
  private handleAnimate = (time: number) => {
    if (this.pauseAnimation) {
      return;
    }

    const currentTime = Date.now();
    const diffTime = currentTime - this.lastTime;

    if (diffTime < this.expectedDiff) {
      return;
    }

    this.lastTime = currentTime;

    for (const line of this.lines) {
      line.material.uniforms.time.value = time / 1000;
    }

    this.renderer.render(this.scene, this.camera);
  };

  protected render() {
    return html`<div id="canvas-wrapper" class="background-3d-wrapper"></div>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    "fabric-ui-parametric-lines": ParametricLines;
  }
}
